@media (max-width: 992px) {
    .distantBlock{
        justify-content: center;
        align-items: center;

    }
    .distantBlock span{
        text-align: center;
    }
}

.distant{
    height: 30vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.distantBlock{
    flex-direction: column;
}
.distantBlock span{
    font-size: 35px;
    font-weight: 600;
}


