.projectBlock{
    width: 350px;
    min-height: 400px;
    padding-top: 15px;
    border: 2px solid #595858;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 15px;
}
.projectBlockIMG{
    width: 300px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.projectBlockIMG img{
    width: 300px;
    height: 200px;

}
.projectBlockButton{
    position: absolute;
    width: 150px;
    height: 40px;
    background: #121519;
    border-color: #121519;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
}
.projectBlockButton a{
    color: #fff;
}
.projectBlock span{
    font-weight: bold;
    font-size: 25px;
    color:#f3f6f2;
}
.projectBlockText{
    width: 90%;
    height: 30%;
    text-align: center;
    color:#f3f6f2;
}

