@media (max-width: 992px) {
    .contactsBlockForm {
        height: 80%;
        max-width: 340px;
        width: 300px;
    }
    .contactsBlock input{
        height: 20px;
    }
    .contactsBlock input{
        height: 40px;
    }
}
.contacts{
    height: 100vh;
    background-color: #777676;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.contactsBlock{
    height: 100%;
    flex-direction: column;
}
.contactsBlock h2{
    color: #f3f6f2;
}
.contactsBlockForm{
    height: 50%;
    max-width: 500px;
    background-color: #a2a1a1;
    border:2px solid #565454;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    gap:20px;

}
.contactsBlock input, textarea{
    width: 200px;
    height: 40px;
    text-decoration: none;
    border-radius: 5px;
}
.contactsBlock textarea{
    width: 200px;
    height: 80px;
    text-decoration: none;
    border-radius: 5px;
}

