@media (max-width: 992px) {
   .footerIcons {
        max-width: 200px;
        min-height: 200px;
        flex-direction: column;
        justify-content: center;
}

    .footerBlock {
        justify-content: center;
    }
}
.footer{
    min-height: 30vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.footerBlock{
    min-height: 100%;
    flex-direction:column;
}
.footerBlock span:first-child{
    font-weight: 600;

}
.footerIcons{
    max-width: 900px;
    min-height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
.footerIcons img{
    width: 30px;
    height: 30px;
    margin-right: 5px;
    margin-left: 10px;
}