.projects{
    margin-top: 70px;
    min-height: 100vh;
    background-color: #777676;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.projectsBlock {
    min-height: 100vh;
    flex-direction: column;
}
.projectsBlock h2{
    color: #f3f6f2;
}
.project{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}