.skillBlock{
    width: 300px;
    min-height: 300px;
    padding-top: 15px;
    border: 2px solid #595858;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap:15px;
    flex-wrap: wrap;
}
.skillBlock img{
    width: 150px;
    height: 100px;
}
.skillBlock span{
    font-weight: bold;
    font-size: 25px;
}
.skillBlockText{
    width: 90%;
    min-height: 40%;
    text-align: center;
}


